.description__bottom {
    display: flex;
    flex-direction: column;
    border-left: 4px solid #e6e6f0;
    padding-left: 30px;

    &__item {
        display: flex;
        flex-direction: column;
    }

    &__item-number {
        font-weight: 700;
        font-size: 36px;
        line-height: 44px;
        background: linear-gradient(90.13deg, #f0145a 0.84%, #ef8080 99.96%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        padding-bottom: 10px;
        display: flex;
        align-items: center;
        &::before {
            content: "";
            background-image: url(../img/sign-flag.svg);
            width: 23px;
            height: 26px;
            margin-right: 13px;
        }
    }

    &__item-number_bag::before {
        background-image: url(../img/sign-bag.svg);
        width: 25px;
        height: 21px;
    }

    &__item-number_star::before {
        background-image: url(../img/sign-star.svg);
        width: 25px;
        height: 26px;
    }

    &__item-title {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #323264;
        padding-bottom: 40px;
    }

    &__item-title_padding {
        padding-bottom: 0px;
    }
}

@media (min-width: 768px) {
    .achievments {
        display: none;
    }
}

@media (min-width: 1024px) {
    .achievments {
        display: inline-block;
    }
}
